import React from 'react';
import Navigation from '../components/Navigation';
import { CopyToClipboard } from 'react-copy-to-clipboard';

const Contact = () => {
    return (
        <div className="contact">
            <Navigation />
            <div className="contactContent">
                <div className="header">
                        <div className="contactBox">
                            <h1>Contactez-moi</h1>
                            <ul>
                                <li>
                                    <i className="fas fa-map-marker-alt"></i>
                                    <span>Bourges</span>
                                </li>
                                <li>
                                    <i className="fas fa-mobile-alt"></i>
                                    <CopyToClipboard text="0612345678">
                                        <span className="clickInput" onClick={() => {alert('T√©l√©phone copi√© !');}}>06 12 34 56 78</span>
                                    </CopyToClipboard>
                                </li>
                                <li>
                                    <i className="far fa-envelope"></i>
                                    <CopyToClipboard text="devfrontend18@gmail.com">
                                        <span className="clickInput" onClick={() => {alert('E-mail copi√© !');}}>devfrontend18@gmail.com</span>
                                    </CopyToClipboard>
                                </li>
                            </ul>
                        </div>
                        <div className="socialNetwork">
                            <ul>
                            
                            <a href="https://www.linkedin.com" target="_blank" rel="noopener noreferrer"><i className="fab fa-linkedin"></i></a>
                        
                        
                            <a href="https://www.github.com" target="_blank" rel="noopener noreferrer"><i className="fab fa-github"></i></a>
                        
                        
                            <a href="https://www.twitter.com" target="_blank" rel="noopener noreferrer"><i className="fab fa-twitter"></i></a>
                        
                        
                            <a href="https://codepen.io/your-work?verified=true" target="_blank" rel="noopener noreferrer"><i className="fab fa-codepen"></i></a>
                        
                            </ul>
                        </div>
                    </div>
                

        </div>
    </div>
    );
};

export default Contact;